// Header

.header
{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: $separacion 0;

    @include desktop 
    {
        flex-direction: row;
        justify-content: space-between;
    }

    &__logo
    {
        width: 30rem;
        margin-bottom: $separacion;

        @include desktop 
        {
            margin-bottom: 0;
        }
    }
}

// Navegacion

.navegacion
{
    display: flex;
    flex-direction: column;
    align-items:center;
    gap: 2rem;
    width: 100%;

    @include desktop 
    {
        flex-direction: row;
        width: auto;
    }

    &__link
    {
        font-size: 2rem;
        font-weight: 700;
        color: $negro;

        &--registrar
        {
            @include boton($primario, $blanco)
        }
    }
}