// Testimonios global

.testimoniales
{
    padding: $separacion 0;


    &__heading
    {
        text-align: center;
    }
    &__grid
    {
        @include tablet 
        {
            @include grid(5, 2rem)
        }
    }
}

// Testimonios

.testimonial
{
    background-color: $secundario;
    padding: 2rem;
    margin-bottom: 2rem;
    border-radius: 1rem;
    color: $blanco;

    &:last-of-type
    {
        margin-bottom: 0;
    }

    @include tablet 
    {
        margin-bottom: 0;
        &:nth-child(1)
        {
            grid-column: 1 / 4;
        }

        &:nth-child(3)
        {
            grid-row: 1 / 3;
            grid-column: 5 / 6;
        }

        &:nth-child(4)
        {
            grid-column: 1 / 3;
        }

        &:nth-child(5)
        {
            grid-column: 3 / 5;
        }
    }

    &__header
    {
        display: flex;
        gap: 2rem;
        align-items: center;
    }

    &__imagen
    {
        flex-basis: 10rem;
        overflow: hidden;
        border-radius: 50%;
    }

    &__autor
    {
        color: $blanco;
        font-weight: 700;
        margin: .5rem 0;
    }

    &__texto
    {
        font-size: 2rem;
        line-height: 1.5;
        margin: 2rem 0;
        padding-left: 3.5rem;
        position: relative;

        &::before
        {
            content: ' " ';
            font-size: 10rem;
            position: absolute;
            top: -4rem;
            left: 0;
            opacity: .2;
            font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        }
    }
}