.pasos
{
    margin-top: 5rem;

    &__wave
    {
        margin-bottom: -1px;
    }

    &__contenido
    {
        background-color: $primario;
    }

    &__heading
    {
        text-align: center;
        color: $blanco;

        &--descripcion
        {
            display: block;
            font-size: 1.8rem;
        }
    }

    &__grid
    {
        
        @include tablet 
        {
            @include grid(3, $separacion);
        }
    }

    &__imagen
    {
        transition-property: transform;
        transition-duration: .3s;

        &:hover
        {
            transform: rotate(-4deg) scale(1.2);
        }
    }
}