.footer
{
    background-color: $secundario;
    padding-top: $separacion;

    &__grid
    {
        
        @include tablet 
        {
            @include grid(3, 4rem);
        }
    }

    &__widget
    {
        margin-bottom: $separacion;

        &:last-of-type
        {
            margin-bottom: 0;
        }
    }

    &__heading
    {
        color: $blanco;
        margin: 0;
        text-align: center;

        @include tablet 
        {
            text-align: left;
        }
    }

    &__texto
    {
        margin: 0;
    }

    &__nav
    {
        display: flex;
        flex-direction: column;
    }

    &__nav,
    &__texto
    {
        margin-top: 2rem;
    }

    &__link,
    &__texto
    {
        font-size: 2rem;
        line-height: 1.8;
        color: $blanco;
        text-align: center;

        @include tablet 
        {
            text-align: left;
        }
    }


    &__copy
    {
        background-color: $primario;
        color: $blanco;
        text-align: center;
        margin: 2rem 0 0 0;
        padding: 2rem;

        @include tablet 
        {
            margin: 0;
        }
    }
}