.favoritos
{
    background-color: $grisClaro;
    padding: $separacion 0;

    &__heading
    {
        text-align: center;
        color: $secundario;
    }

    &__grid
    {
        @include tablet 
        {
            @include grid(2, 4rem)
        }
    }
}

.favorito
{
    background-color: $blanco;
    padding: 4rem;
    margin-bottom: 2rem;
    box-shadow: 0px 0px 2.6rem -.8rem darken( $grisClaro, 38% ) ;
    border-radius: 1rem;

    &:last-of-type
    {
        margin-bottom: 0;
    }

    @include tablet 
    {
        margin-bottom: 0;
    }

    &__grid
    {
        @include tablet 
        {
            display: grid;
            grid-template-columns: 2fr 3fr;
            gap: 2rem;
            align-items: center;
        }

        @include desktop 
        {
            grid-template-columns: 1fr 3fr;
            gap: 4rem;
        }
    }

    &__imagen
    {
        max-width: 20rem;
        margin: 0 auto 2rem auto;
    }

    &__contenido
    {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include tablet 
        {
            align-items: flex-start;
        }
    }

    &__nombre
    {
        margin: 1rem 0;
        color: $primario;
        font-size: 2.4rem;
    }

    &__descripcion
    {
        margin: .5rem 0;
        text-align: center;
        font-size: 1.6rem;
        line-height: 1.7;
        color: $secundario;

        @include tablet 
        {
            text-align: left;
        }
    }
}