.ganadinero
{
    padding: $separacion;

    &__heading
    {
        text-align: center;
        color: $secundario;
    }

    &__grid
    {

        @include tablet 
        {
            @include grid(2, 4rem);
            align-items: center;
        }
    }

    &__info
    {
        margin-top: 4rem;
    }

    &__descripcion
    {
        margin: 0 0 2rem 0
    }

    &__boton
    {
        @include boton($primario, $blanco);
        
        @include tablet 
        {
            width: 25rem;   
        }
    }
}