.formulario
{

    &__grid
    {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: $separacion;

        @include tablet 
        {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
        }
    }
}

.formulario__contenido
{
    background-color: $primario;
    padding: 10rem 5rem;

    h2
    {
        color: $blanco;
        text-align: center;
    }

    .formulario__buscar
    {
        color: $blanco;
        text-transform: uppercase;

        .formulario__campo
        {
            margin-top: 1rem;

            .formulario__input
            {
                display: block;
                padding: 1rem;
                margin-top: 1rem;
                width: 100%;
            }
        }

        .formulario__submit
        {
            @include boton($secundario, $blanco);
            margin-top: 2rem;

            @include tablet 
            {
                width: 100%;
            }
        }
    }
}