.app
{
    background-color: $grisClaro;
    padding: $separacion 0;

    &__grid
    {

        @include tablet 
        {
            @include grid(2, 4rem);
            align-items: center;
        }
    }

    &__imagen
    {
        width: 50%;
        margin: 0 auto;
        transition-property: transform;
        transition-duration: .3s;

        &:hover
        {
            transform: rotate(-4deg) scale(1.2);
        }
    }
}

.tiendas
{
    margin-top: $separacion;

    @include tablet 
    {
        margin-top: 0;
    }

    &__heading
    {
        color: $secundario;
        text-align: center;
        margin: 0;
    }

    &__texto
    {
        text-align: center;
        font-size: 1.6rem;
    }

    &__grid
    {
        @include grid(2, 3rem);
    }

    &__enlace
    {
        transition-property: transform;
        transition-duration: .5s;

        &:hover
        {
            transform: scale(1.2);
        }
    }
}